import * as React from 'react';
import { Stack } from '@chakra-ui/core';
import Main from '~/components/Main';
import Intro from '~/components/home/Intro';
import Description from '~/components/home/Description';
import InEvidenza from '~/components/home/InEvidenza';
import News from '~/components/home/News';
import Promo from '~/components/home/Promo';
import Partners from '~/components/home/Partners';

import client, { HOMEPAGE_QUERY } from '~/graphql';
import fetch from 'node-fetch';
import fs from 'fs';
import Carousel from '~/components/Carousel/Carousel';

const newsEndpoint = 'http://webservice.columbusinformatica.it/api/news';
const offerteEndpoint = 'http://webservice.columbusinformatica.it/api/offerte';

export async function getStaticProps() {
  const descriptionData = await client.request(HOMEPAGE_QUERY);
  const newsData = await fetch(newsEndpoint).then((res) => res.json());
  const offerteData = await fetch(offerteEndpoint).then((res) => res.json());
  const partnersImages = fs.readdirSync('public/static/images/partners');
  const [firstRow, secondRow] = partnersImages.reduce(
    (acc, curr, index) => {
      if (index % 2 === 0) acc[0].push(curr);
      else acc[1].push(curr);
      return acc;
    },
    [[], []]
  );
  return {
    props: {
      ...descriptionData,
      news: newsData,
      offerte: offerteData,
      firstRow,
      secondRow,
    },
  };
}

export default ({ homepage, news, offerte, firstRow, secondRow }) => {
  return (
    <Main isHome>
      <Stack overflowX="hidden" spacing={8}>
        <Intro />
        <Description data={homepage.cards} />
        {/* <InEvidenza /> */}
        <News data={news} skew={3} />
        <Promo data={offerte} />
        <Carousel>
          <div style={{ width: 500, height: 200, backgroundColor: 'blue' }}>ALALALA</div>
          <div style={{ width: 500, height: 200, backgroundColor: 'red' }}>ALALALA</div>
          <div style={{ width: 500, height: 200, backgroundColor: 'green' }}>ALALALA</div>
          <div style={{ width: 500, height: 200, backgroundColor: 'purple' }}>ALALALA</div>
          <div style={{ width: 500, height: 200, backgroundColor: 'yellow' }}>ALALALA</div>
          <div style={{ width: 500, height: 200, backgroundColor: 'pink' }}>ALALALA</div>
        </Carousel>
        <Partners firstRow={firstRow} secondRow={secondRow} />
      </Stack>
    </Main>
  );
};
